@media (min-width: 1344px) {
  .App {
    .container {
      .video {
        height: 150vh;
      }

      .content {
        .subContent {
        }
      }
      .header {
        height: 7rem;
        .nav {
          width: 40%;
          margin-right: 20%;

          .navList {
            .navLink {
              a {
                font-size: 1.8rem;
              }
            }
          }
        }
      }
    }

    .main {
      .aboutMe {
        width: 100%;
        flex-flow: row nowrap;
        background-color: whitesmoke;
        padding-top: 7rem;

        .skillsBarsContainer {
          width: 40%;
          max-height: 40vh;
          align-self: flex-start;
          margin-left: 10%;
        }

        .infoContainer {
          display: flex;
          width: 50%;
          align-self: flex-end;
          height: 100vh;
          .bottom {
            display: none;
          }
          .leftRight {
            display: inline;
          }
          .infoDiv {
            h3 {
              font-size: 200%;
              padding: 0.5rem;
            }
            p {
              font-size: 150%;
              padding: 0.3rem;
              width: 80%;
            }
          }
        }
      }
      .portfolio {
        width: 100%;
        padding: 5rem;
        padding-top: 9rem;
        background-color: whitesmoke;
        .cards-container {
          .link {
            height: 300px;
            .card {
              .card-image {
                height: 200px;
              }
              .card-body {
                height: 100px;
                .title {
                  font-size: 2rem;
                }
                .description {
                  font-size: 1.5rem;
                }
              }
            }
          }
        }
      }
      .contactMe {
        width: 100%;
        background-color: whitesmoke;
        padding-top: 11rem;
      }
    }
  }
}
